.modal-todos{
    position: absolute;
    min-width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    border: 1px solid rgb(189, 189, 189);
    border-radius: 10px;
    padding: 20px;
    box-shadow: 1px 1px 5px rgb(189, 189, 189);
}